
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
html,
body {
    height: 100%;
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    background: rgb(245, 245, 245);
    overflow: hidden;
}
input[type='checkbox'] {
    /*visibility: hidden;*/
    margin: 0px 10px;
    height: 25px;
    width: 25px
}
.todo-app__root {
    margin: 0 auto;
    /* margin-top: 7em; */
    width: 50em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 1em;
    color: #4d4d4d;
    font-weight: 300;
}
.todo-app__header {
    margin-bottom: 1em;
}
.todo-app__title {
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(255, 148, 181, 0.8);
    margin: 0;
    padding: 0;
}
.todo-app__main {
    width: 100%;
}
.todo-app__input {
    padding: 16px;
    border: none;
    color: inherit;
    background: rgba(254, 254, 254);
    font-size: 24px;
    font-weight: 200;
    line-height: 1.4em;
    width: 100%;
    background: rgb(254, 254, 254);
    -webkit-box-shadow: inset 0px 0px 10px 2px rgba(0, 0, 0, 0.048);
            box-shadow: inset 0px 0px 10px 2px rgba(0, 0, 0, 0.048);
    border: 1px solid rgba(0, 0, 0, 0.089);
    height: 5rem;
}
.todo-app__input:focus {
    outline: none;
}
.todo-app__input::-webkit-input-placeholder {
    font-weight: 100;
    opacity: 0.6;
}
.todo-app__input::-moz-placeholder {
    font-weight: 100;
    opacity: 0.6;
}
.todo-app__input::-ms-input-placeholder {
    font-weight: 100;
    opacity: 0.6;
}
.todo-app__input::placeholder {
    font-weight: 100;
    opacity: 0.6;
}
.todo-app__main {
    background: rgb(254, 254, 254);
}
.todo-app__list {
    list-style: none;
    padding: 0;
    margin: 0;
    /* 5*5=25 */
    max-height: 23em;
    overflow: scroll;
}
.todo-app__item {
    position: relative;
    width: 100%;
    min-height: 5em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.089);
    border-top: 0;
}
.todo-app__checkbox {
    width: 30px;
    height: 30px;
    background: #ddd;
    margin: 15px;
    border-radius: 100%;
    position: relative;
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.312);
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.312);
}
.todo-app__checkbox label {
    width: 20px;
    height: 20px;
    border-radius: 100px;

    -webkit-transition: all 0.5s ease;

    transition: all 0.5s ease;
    cursor: pointer;
    position: absolute;
    top: 5px;
    left: 5px;
    z-index: 1;

    background: rgba(99, 99, 99, 0.698);
    -webkit-box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.312);
            box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.312);
}
.todo-app__checkbox input[type='checkbox']:checked + label {
    /*background: #26ca299b;*/
    background: #80d1ff;
}
.todo-app__item-detail {
    font-weight: 300;
    overflow-wrap: break-word;
    width: 90%;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    height: 100%;
}
.todo-app__footer {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 3em;
    padding: 1em;
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.089);
    border-top: 0.5px solid rgba(0, 0, 0, 0.089);
}
.todo-app__total {
    min-width: 3em;
}
.todo-app__view-buttons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0;
    margin-left: 2em;
    padding: 0;
    width: 12em;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    list-style: none;
}
.todo-app__view-buttons button {
    border: none;
    padding: 5px;
    font-size: 0.8em;
    font-weight: inherit;
    border: 1px solid transparent;
    border-radius: 5px;
    cursor: pointer;
    background: #80d1ff70;
}
.todo-app__view-buttons button:focus {
    outline: none;
}
.todo-app__clean {
    vertical-align: middle;
}
.todo-app__clean button {
    border: none;
    padding: 5px;
    border: 1px solid transparent;
    font-size: 0.8em;
    font-weight: inherit;
    cursor: pointer;
    border-radius: 5px;
    background: #80d1ff70;
}
.todo-app__clean button:focus {
    outline: none;
}
.todo-app__clean button:hover {
    text-decoration: underline;
}
.todo-item-input {
    font-size: 1.5em;
    font-weight: 300;
    overflow-wrap: break-word;
    width: 80%;
    min-height: 3em;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    border: none;
}
.todo-item-input:focus {
    outline: none;
}
.todo-app__item-x {
    display: none;
    border-radius: 10px;
    border:solid transparent;
}
.todo-app__item:hover .todo-app__item-x {
    display: inline;
    position: absolute;
    max-width: 20px;
    max-height: 20px;
    right: 20px;
}
.todo-app__item:hover .todo-app__item-x:hover {
    max-width: 25px;
    max-height: 25px;
    right: 17.5px;
}
.red{
    text-decoration: line-through;
}

